body{
  margin: 0;
  padding: 0;
  background-color: #0f171e;
}

*{
  box-sizing: border-box;
  
}
.container{
  padding: 10px;
  text-align: center;
  color:white;
padding:0;
}
.title{
  font-size: 3rem;
  font-family: 'Playfair Display', serif;
}

 

@media(max-width:500px){
  body{
    background: url("https://images.pexels.com/photos/1624496/pexels-photo-1624496.jpeg?cs=srgb&dl=bird-s-eye-photography-of-mountain-1624496.jpg&fm=jpg"),black;
    background-size: 600px 800px;
    background-repeat: no-repeat;
  
  }
  .container .title{
    font-size: 3rem;
    color: white;
  }
  .container .form{
    display: flex;
    flex-direction: column;
  }
  .container .label{
    display: none;
  }
  .container .input{
    width: 100%;
    margin: 0;
  }
  .container .button{
    margin-left: 21%;
    margin-top: 5%;
    width: 200px;
  }
  .container .image{
    width:300px;
    margin-top: 10px;
  }
}
.form{
  display: grid;
  grid-template-columns:auto 1fr auto ;

}
.label{
  font-size: 2rem;
}
.input{
  height: 50px;
  border: 1px solid #ddd;
  border-radius:20px ;
  line-height: 2.8rem;
  font-size: 1.2rem;
  margin: 0 2rem;
}
input:focus{
  outline: none;
}
.button{
  width: 300px;
  border: 1px solid rgba(0, 0,0,0.7);
  background-color: rgba(0, 0,0,0.7);
  color: white;
  font-size: 2rem;
  border-radius: 20px;
}
.button:focus{
  outline: none;
}
.button:hover{
background-color: rgba(0, 0, 0, 0.5);
}

.cards{
margin: 50px;
width: 400px;
background-color:#0f171e;
color: white;
border-radius: 3px;
padding-top: 20px;
margin-left: 6rem;
}
.image{
  width: 300px;
  height: 300px;
}

.cards_main{
  position: absolute;
  margin-top: 100px;
  display: grid;
  grid-template-columns: auto auto auto;
  font-family: 'Playfair Display', serif;
 margin-right: 0;
}
@media(max-width: 1505px){
  .container .cards_main{
    grid-template-columns: auto auto;
  }
}
@media(max-width:500px){
  .container .cards_main{
    display: flex;
    flex-direction: column;
    margin: 0;
    background: url("./assets/image1.jpg"),grey;
    background-size: 600px 800px;
    background-repeat: repeat;
    margin-top: 100px;
    margin-right: 0px;
  }
  .container .cards:hover{
    box-shadow: 0px 0px 0px 0px rgb(177, 175, 175);
   }
  .container .cards{
  margin: 0;
  margin-top: 20px;
  width: 100%;
  }
  .cards_error_paragraph{
    color: white;

    text-align:center;
    font-size:"0.7rem";
    width:99vw;
    
  }
}

.movie_desc{
  font-size: 0.8rem;
}

.cards_error_paragraph{
  color: white;
 
  
  text-align:center;
  font-size:"0.7rem";
  width:99vw;
}
.searchBox {
  position: absolute;
  display: fixed;
  top: 13%;
  left: 50%;
  transform:  translate(-50%,50%);
  background: #2f3640;
  height: 60px;
  border-radius: 40px;
  padding: 10px;

}


.searchBox:hover > .searchInput {
  width: 240px;
  padding: 0 6px;
}

.searchBox:hover > .searchButton {
background: white;
color : #2f3640;
}

.searchButton {
  color: white;
  float: right;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #2f3640;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s;
  
}
.searchButton:focus{
  outline: none;
}

.searchInput {
  border:none;
  background: none;
  outline:none;
  float:left;
  padding: 0;
  color: white;
  font-size: 16px;
  transition: 0.4s;
  line-height: 40px;
  width: 0px;

}

@media screen and (max-width: 620px) {
.searchBox:hover > .searchInput {
  width: 120px;
  padding: 0 6px;
}

}